.footer {
  background-color: $brown-light;
  padding-top: 75px;
  padding-bottom: 45px;
  color: $gray;
  @include lg {
    padding-top: 60px; }
  @include md {
    padding-top: $padding-md;
    padding-bottom: $padding-md; }
  @include sm {
    padding-top: $padding-sm;
    padding-bottom: $padding-sm; }
  &__logo {
    padding-left: 40px;
    margin-bottom: 15px;
    @include sm {
      text-align: center;
      padding-left: 0; } }
  &__block {
    @include md {
      margin-bottom: 15px; } }
  &__descr {
    font-size: $fz-xs;
    margin-bottom: 25px;
    max-width: 270px;
    @include sm {
      max-width: none;
      margin-bottom: 15px; } }
  &__auth {
    font-size: $fz-xs;
    a {
      display: inline-block;
      vertical-align: middle; } }
  &__title {
    color: $c-text;
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 15px; }
    @include xs {
      font-size: 18px;
      h2, .h2 {
        font-size: 18px; } } }
  &__copy {
    padding-top: 60px;
    text-align: center;
    font-size: $fz-xs;
    @include lg {
      padding-top: 40px; }
    @include md {
      padding-top: 30px; }
    span {
      color: $accent; } } }
