.header {
  // background-color: $white
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 10;
  &__inner {
    height: $header-height;
    padding-top: 10px;
    padding-bottom: 10px;
    @include flex(space-between, center);
    @include lg {
      height: $header-height-lg; }
    @include md {
      height: $header-height-md; } }
  &__top {
    box-shadow: 0px 1px 9px 0px rgba(0, 0, 0, 0.14);
    background-color: $white;
    position: relative;
    z-index: 1; }
  &__logo {
    width: percentage(2/12);
    @include lg {
      width: auto; } }
  &__title {
    @include bold;
    font-size: $fz-lg;
    @include xl {
      display: none; } }
  &__btns {
    flex-grow: 1;
    @include flex(space-around, center);
    @include xl {
      justify-content: flex-start;
      .btn {
        margin-right: 15px; } }
    @include md {
      display: none; } }
  &__btns-mob {
    display: none;
    @include md {
      @include flex(center, center, column);
      margin-top: 10px;
      margin-bottom: 10px;
      .btn {
        margin-bottom: 10px; } } }
  &__contacts {
    @include xl {
      flex-grow: 1;
      text-align: right; }
    @include md {
      text-align: left; } }
  &__nav {
    flex-grow: 1;
    position: relative;
    @include lg {
      position: fixed;
      z-index: 15;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($black-darkest, .5);
      visibility: hidden;
      opacity: 0;
      @include tr(opacity .3s, visibility .3s);
      &.is-active {
        visibility: visible;
        opacity: 1;
        .header__nav-inner {
          transform: translate(0, 0); } } } }

  &__nav-inner {
    @include lg {
      background-color: $white;
      position: relative;
      height: 100%;
      max-width: 90%;
      padding: 20px;
      padding-top: 50px;
      @include flex(center, center, column);
      overflow: auto;
      transform: translate(-100%, 0);
      @include tr(transform .3s); }
    @include rh(667) {
      justify-content: flex-start !important; } }
  &__nav-close {
    @include icon-close(20, 2, currentColor);
    z-index: 1;
    display: none;
    position: absolute;
    top: 20px;
    right: 20px;
    @include lg {
      display: block; } }
  &__controls {
    width: percentage(2/12);
    @include flex(flex-end, center);
    @include lg {
      width: auto; } }
  &__burger {
    margin-left: 10px;
    position: relative; }
  &__cart {
    margin-left: 40px;
    @include md {
      margin-left: 20px; }
    @include xs {
      margin-left: 10px; } }
  &__bottom {
    @include tr(transform .3s);
    background-color: $white;
    @include xs {
 }      // display: none
    &.is-hidden {
      transform: translate(0, -100%); } } }


.cart-btn {
  color: $gray;
  position: relative;
  display: inline-block;
  @include tr(color .3s);
  @include hover {
    color: $purple; }
  .icon {
    font-size: 21px; }
  sup {
    display: inline-block;
    vertical-align: top;
    font-size: 10px;
    color: $c-text;
    background-color: $yellow;
    border-radius: 50%;
    height: 16px;
    width: 16px;
    line-height: 16px;
    text-align: center;
    position: relative;
    top: -.8em;
    left: -.5em; } }

.burger {
  color: $gray;
  @include tr(color .3s);
  @include hover {
    color: $purple; }
  .icon {
    font-size: 21px; }
  &.is-active {
    color: $purple; } }

.lang {
  $this: &;
  color: $gray;
  position: relative;
  padding-right: 15px;
  @include before {
    @include icon-arrow(10, 10, 1, currentColor, -45);
    position: absolute;
    right: 0;
    top: .3em;
    pointer-events: none; }
  &:hover {
    #{$this}__list {
      visibility: visible;
      opacity: 1; } }
  &__title {}
  &__list {
    position: absolute;
    width: 100%;
    left: 0;
    top: 100%;
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    @include tr(opacity .3s, visibility .3s); } }
