strong {
  // font-weight: 400
 }  // +bold

small {
  font-size: inherit; }

em, i {
  font-style: normal; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.h1, h1 {
  // font-size: 36px
  @include css-lock(21, 36, 320, 1199, font-size);
  font-weight: 700;
  strong {
    @include clar;
    font-weight: 400;
    sup {
      vertical-align: super;
      font-size: .5em; } }

  &--purple {
    color: $purple; }
  &--accent {
    color: $accent; }
  &--ttu {
    text-transform: uppercase; } }

.h2, h2 {
  font-size: 32px;
  font-weight: 700;
  line-height: 1.2;
  @include css-lock(21, 32, 320, 1199, font-size);

  &--accent {
    color: $accent; } }

.h3, h3 {
  font-size: $fz-lg;
  font-weight: 700;
  line-height: 1.2; }

.h4, h4 {}

.h5, h5 {}

.h6, h6 {}

.title {
  font-weight: 700;
  text-transform: uppercase;
  small {
    font-size: $fz-sm;
    font-weight: 300;
    @include sm {
      font-size: $fz-xs; }
    @include xs {
      font-size: 11px; } }
  &--lg {
    // font-size: 60px
    @include css-lock(24, 60, 320, 1199, font-size); }
  &--md {
    @include css-lock(21, 45, 320, 1199, font-size); } }

.title-under {
  position: relative;
  padding-bottom: 20px;
  @include before {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    height: 7px;
    width: 30px;
    background-color: $accent;
    border-radius: 7px; }
  @include xs {
    padding-bottom: 15px; }
  &--green {
    @include before {
      background-color: $green-dark; } }
  &--green-lighten {
    @include before {
      background-color: $green-lighten; } }
  &--left {
    @include before {
      left: 0;
      transform: translate(0, 0); } }
  &--yellow {
    @include before {
      background-color: $yellow; } }
  &--inherit {
    @include before {
      background-color: currentColor; } } }

.title-bg {
  color: $white;
  display: inline-block;
  position: relative;
  @include sm {
    color: inherit; }
  span {
    position: relative;
    z-index: 1; }
  img {
    @include vcenter;
    width: 105%;
    max-width: 105%;
    z-index: 0;
    pointer-events: none;
    @include sm {
      display: none; } } }
