// +font('comfortaa-regular', 'comfortaa-regular-webfont')
// +font('comfortaa-bold', 'comfortaa-bold-webfont')
// +font('comfortaa-light', 'comfortaa-light-webfont')
@include font('clarendon-black', 'tt0351m_-webfont');
@include font('pfreminderpro-medium', 'pfreminderpro-medium-webfont');
@include font('ability_linear45', 'ability_linear45-webfont');

@mixin reg {
  // font-family: 'comfortaa-regular', Arial, sans-serif
  font-family: 'Comfortaa', cursive;
  font-weight: 400; }
@mixin bold {
  // font-family: 'comfortaa-bold', Arial, sans-serif
  font-weight: 700; }
// =light
//   font-family: 'comfortaa-light', Arial, sans-serif
@mixin clar {
  font-family: 'clarendon-black', Arial, sans-serif; }
@mixin pfr {
  font-family: 'pfreminderpro-medium', Arial, sans-serif; }
@mixin ability {
  font-family: 'ability_linear45', Arial, sans-serif; }
