$icons: map-merge($icons,('arrow-left': (X: 0px, Y:-264px, W: 154px, H: 70px, TW: 536px, TH: 334px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('arrow-right-white': (X: 0px, Y:-132px, W: 266px, H: 122px, TW: 536px, TH: 334px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('comet': (X: -276px, Y:0px, W: 124px, H: 142px, TW: 536px, TH: 334px, IMG: '../img/sprite.png')));
