.testimonial {
  background-color: $brown-light;
  border-radius: $radius;
  padding: 60px 35px 35px 120px;
  color: $gray;
  @include flex(space-between, normal, column);
  height: 100%;
  font-size: $fz-sm;
  @include lg {
    padding: 30px 15px 15px 60px; }
  @include xs {
    padding: 20px 15px 15px 40px; }
  &__quote {
    margin-bottom: 20px;
    @include xs {
      margin-bottom: 10px; } }
  &__bottom {
    @include flex(flex-start, center); }
  &__img {
    @include cover;
    width: 90px;
    height: 90px;
    flex: 0 0 90px;
    border-radius: 50%;
    display: block;
    border: 1px solid  $gray-light;
    margin-right: 25px;
    @include xs {
      width: 70px;
      height: 70px;
      flex: 0 0 70px; } }
  &__title {
    font-size: 21px;
    font-weight: 700;
    margin-bottom: 5px;
    @include xs {
      font-size: 18px; } }
  &__subttl {} }
