.about-items {
  @include flex(center, normal, row, wrap); }

.about-item {
  $this: &;
  padding-left: 15px;
  padding-right: 15px;
  width: 50%;
  @include flex();
  margin-bottom: 30px;
  color: $white;
  padding-top: 40px;
  @include md {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    margin-bottom: 15px; }
  @include xs {
    padding-top: 20px; }
  &__inner {
    @include cover;
    position: relative;
    padding-top: 65%;
    width: 100%;
    border-radius: $radius;
    @include before {
      @include coverdiv;
      z-index: 0;
      border-radius: $radius;
      background-color: rgba($purple, .6); }
    @include md {
      padding-top: 40%; }
    @include xs {
      padding-top: 60%; }
    @include xss {
      padding-top: 100%; } }
  &__label {
    position: absolute;
    z-index: 1;
    top: -40px;
    right: 0;
    border-radius: $radius;
    background-color: $gray;
    height: 235px;
    width: 200px;
    @include flex(center, center, column);
    text-align: center;
    font-weight: 700;
    font-size: 32px;
    @include lg {
      width: 190px;
      height: 180px; }
    @include md {
      font-size: 28px; }
    @include sm {
      font-size: $fz-lg;
      // width: 10px
      height: 100px; }
    @include xs {
      // font-size: $fz
      // height: 60px
      top: -20px; }
    small {
      font-size: 18px;
      margin-bottom: 15px; }
    .icon {
      font-size: 80px; }
    &.is-yellow {
      background-color: $yellow; }
    &.is-blue {
      background-color: $purple; }
    &.is-purple {
      background-color: $purple-accent; } }
  &__title {
    position: absolute;
    z-index: 1;
    bottom: 30px;
    left: 0;
    padding-left: 30px;
    padding-right: 30px;
    @include sm {
      padding-left: 15px;
      padding-right: 15px; } }

  &--text-top {
    #{$this}__title {
      top: 30px;
      bottom: auto;
      max-width: calc(100% - 200px);
      @include lg {
        max-width: calc(100% - 180px); }
      @include md {
        top: auto;
        bottom: 30px;
        max-width: none; } } } }
