.nav {
  @include lg {
    text-align: center; }
  ul {
    @include flex(space-around, center);
    @include lg {
      display: block; } }
  li {
    display: inline-block;
    @include lg {
      display: block;
      @include notlast {
        margin-bottom: 15px; } } }

  a {
    display: block;
    position: relative;
    color: $gray;
    @include tr(color .3s);
    padding-top: 30px;
    padding-bottom: 30px;
    @include bold;
    @include hover {
      color: $purple;
      .ic {
        visibility: visible;
        opacity: 1; } }
    @include lg {
      padding: 0; }
    .ic {
      visibility: hidden;
      opacity: 0;
      @include vcenter;
      z-index: 0;
      @include tr(visibility .3s, opacity .3s); }
    span {
      position: relative;
      z-index: 1; } }

  &--footer {
    @include lg {
      text-align: left; }
    ul {
      display: block; }
    li {
      display: block;
      @include notlast {
        margin-bottom: 20px;
        @include sm {
          margin-bottom: 10px; } } }
    a {
      padding: 0;
      font-size: $fz-sm;
      font-weight: 300;
      @include hover {
        color: $accent; } } }
  &--sm {
    font-size: $fz-sm;
    ul {
      display: block; }
    li {
      display: block;
      @include notlast {
        margin-bottom: 15px; }
      @include lg {
        display: inline-block;
        margin-right: 5px;
        margin-left: 5px;
        @include notlast {
          margin-bottom: 0; } } }
    a {
      padding: 0;
      @include hover {
        color: $accent; } } } }
