.how {
  $this: &;
  color: $white;
  position: relative;
  margin-bottom: 125px;
  @include lg {
    margin-bottom: 100px; }
  @include md {
    margin-bottom: 75px; }
  @include sm {
    padding: $padding-sm 0;
    margin-bottom: 0; }
  &__cards {
    @include flex(flex-start, center);
    padding-top: $padding-lg;
    @include sm {
      padding-top: 0;
      display: block; } }
  &__block {
    height: 100vh;
    @include flex(center, normal, column);
    position: relative;
    @include sm {
      display: block;
      height: auto;
      margin-bottom: 15px; } }
  &__arrow {
      position: absolute;
      top: 25px;
      right: calc(100% + 70px);
      @include lg {
        right: calc(100% + 15px); }
      @include md {
        display: none; } }
  &__block-right {
    // text-align: right
    // max-width: 375px
    // margin-left: auto
    padding-left: 50%;
    @include lg {
      padding-left: 30%; }
    @include md {
      padding-left: 0; } }
  &__block-in {
    position: relative; }
  &__bg {
    @include cover;
    background-size: auto 100%;
    position: absolute;
    z-index: 0;
    top: 0;
    bottom: 0;
    @include sm {
      display: none; } }
  &__title {
    margin-bottom: 15px; }
  &__form {
    height: 100%;
    @include flex(flex-end, center);
    @include sm {
      display: block; } }
  &__left {
    position: absolute;
    left: 0;
    top: 20px;
    bottom: 20px;
    width: 60%;
    z-index: 0;
    @include cover;
    background-position: 50% 0;
    @include md {
      background-position: 30% 0; }
    @include sm {
      display: none; } }


  &--price {
    @include before {
      background-color: $yellow-dark;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      width: 35%;
      z-index: 0;
      @include lg {
        width: 60%; }
      @include sm {
        width: 100%; } }
    #{$this}__bg {
      right: 0;
      width: 50%;
      // background-size: contain
      background-position: 0 50%;
      @include lg {
        width: 75%; } } }

  &--form {
    @include before {
      background-color: $green-light;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      width: 55%;
      z-index: 0;
      @include lg {
        width: 50%; }
      @include sm {
        width: 100%; } }
    #{$this}__bg {
      left: 0;
      width: 70%;
      // background-size: contain
      background-position: 100% 50%;
      @include lg {
        width: 75%; } }
    #{$this}__arrow {
      right: auto;
      left: calc(100% + 150px);
      top: 20px;
      @include lg {
        left: calc(100% + 15px); } }
    #{$this}__block {
      // max-width: 350px
      padding-right: 50%;
      @include lg {
        padding-right: 30%; }
      @include md {
        padding-right: 0; } } }

  &--contacts {
    @include before {
      background-color: $blue-light;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      width: 35%;
      z-index: 0;
      @include lg {
        width: 60%; }
      @include sm {
        width: 100%; } }
    .cloud {
      top: 20px;
      left: 30%;
      position: absolute;
      width: 35%;
      height: 50%;
      max-height: 265px;
      @include lg {
        display: none; } }
    #{$this}__bg {
      right: 0;
      width: 50%;
      z-index: 1;
      // background-size: contain
      background-position: 0 50%;
      @include lg {
        width: 75%; } }
    #{$this}__arrow {
      right: calc(100% + 40px);
      top: 15px; }
    #{$this}__block-in {
 }      // max-width: 330px
    #{$this}__left {
      z-index: 0; } } }

