.social {
  color: $gray-light;
  li {
    display: inline-block;
    @include notlast {
      margin-right: 20px; }
    .icon {
      font-size: 21px;
      vertical-align: middle; }
    .icon-youtube {
      font-size: $fz-lg; } }

  a {
    display: block;
    @include tr(color .3s);
    @include hover {
      color: $accent; } } }
