.info-items {
  @include flex(space-around, center, row, wrap); }

.info-item {
  @include flex(flex-start, center);
  color: $black;
  width: 33.33333%;
  padding-right: 15px;
  @include sm {
    width: 100%;
    margin-bottom: 15px;
    padding-right: 0; }
  &__icon {
    @include inline-flex(center, center);
    border-radius: 50%;
    height: 85px;
    width: 85px;
    flex: 0 0 85px;
    margin-right: 30px;
    @include lg {
      margin-right: 20px; }
    @include md {
      margin-right: 15px;
      height: 70px;
      width: 70px;
      flex: 0 0 70px; }
    @include sm {
      margin-right: 10px; }
    .icon {
      fill: $white;
      font-size: 50px; }
    .icon-like {
      margin-top: -.2em; }
    &.is-red {
      background-color: $red; }
    &.is-green {
      background-color: $green; }
    &.is-orange {
      background-color: $orange; } }
  &__title {
    font-weight: 700; } }
