body {
  background: #FFF;
  line-height: 1.4;
  font-size: $fz;
  // +light
  font-family: 'Comfortaa', cursive;
  font-weight: 300;
  color: $c-text;
  @include no-scroll;
  @include sm {
    font-size: $fz-sm; } }

input, textarea, button {
  font-family: inherit;
  color: inherit;
  @include placeholder {
    font-family: inherit; } }

.bg-contain {
  background-size: contain; }

.pt-xl {
  padding-top: $padding-xl;
  @include lg {
    padding-top: $padding-lg; }
  @include md {
    padding-top: $padding-md; }
  @include sm {
    padding-top: $padding-sm; } }
.pt-lg {
  padding-top: $padding-lg;
  @include md {
    padding-top: $padding-md; }
  @include sm {
    padding-top: $padding-sm; } }
.pt-md {
  padding-top: $padding-md;
  @include sm {
    padding-top: $padding-sm; } }
.pt-sm {
  padding-top: $padding-sm; }

.pb-xl {
  padding-bottom: $padding-xl;
  @include lg {
    padding-bottom: $padding-lg; }
  @include md {
    padding-bottom: $padding-md; }
  @include sm {
    padding-bottom: $padding-sm; } }
.pb-lg {
  padding-bottom: $padding-lg;
  @include md {
    padding-bottom: $padding-md; }
  @include sm {
    padding-bottom: $padding-sm; } }
.pb-md {
  padding-bottom: $padding-md;
  @include sm {
    padding-bottom: $padding-sm; } }
.pb-sm {
  padding-bottom: $padding-sm; }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.out {
  // overflow: hidden
  padding-top: $header-height * 2;
  @include lg {
    padding-top: $header-height-lg * 2; }
  @include md {
    padding-top: $header-height-md * 2; } }

.container {
  max-width: 1440px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto; }

.logo {
  display: inline-block;
  font-size: 0;
  &--header {
    img {
        max-height: 100%; }
    @include lg {
      height: $header-height-lg - 20px; }
    @include md {
      height: $header-height-md - 20px; } } }


.phones {
  @include xl {
 } }    // +flex()

.phone {
  display: block;
  color: $purple;
  @include bold;
  @include hover {
    text-decoration: underline; }
  @include xl {
    display: inline-block;
    @include notlast {
      margin-right: 10px; } }
  &__icon {
    display: inline-block;
    width: 15px;
    .icon {
      font-size: $fz-ssm; } } }

.tm {
  @include clar;
  font-weight: 400;
  sup {
    font-size: .5em;
    vertical-align: super; }
  &--text {
    color: $c-text; }
  &--inherit {
    font-family: inherit;
    font-weight: inherit; } }

.cloud {
  @include pfr;
  color: $accent;
  font-size: 25px;
  @include css-lock(17, 25, 992, 1199, font-size);
  width: 300px;
  height: 265px;
  @include cover;
  background-size: contain;
  @include flex(center, center);
  &__title {
    text-align: center;
    // padding-left: 30px
    margin-top: -30px;
    strong {
      font-weight: 400;
      font-size: 33px;
      display: block; } }
  &--black {
    color: $black; } }

.blockquote {
  position: relative;
  &__icon {
    position: absolute;
    top: 0;
    right: calc(100% + 15px);
    .icon {
      fill: $accent;
      font-size: 35px;
      @include xs {
        font-size: 20px; } } } }

.counter {
  color: $gray;
  font-size: 0;
  input, button {
    display: inline-block;
    vertical-align: top; }
  input {
    width: 60px;
    height: 37px;
    border: 1px solid  $c-border;
    font-size: $fz;
    padding-left: 5px;
    padding-right: 5px;
    text-align: center; }
  button {
    background-color: $white;
    font-size: $fz;
    height: 37px;
    width: 35px;
    border: 1px solid  $c-border;
    // +inline-flex(center, center)
    line-height: 35px;
    font-size: $fz-lg;
    font-weight: 100;
    &:first-child {
      border-top-left-radius: 30px;
      border-bottom-left-radius: 30px; }
    &:last-child {
      border-top-right-radius: 30px;
      border-bottom-right-radius: 30px; } } }

.price {
  @extend .h2;
  color: $yellow;
  @include sm {
    small {
      font: $fz-sm; } }
  small {
    font-size: $fz;
    color: $gray; } }

.section-cursive-text {
  @include ability;
  @include css-lock(45, 85, 320, 1199, font-size);
  color: $gray-dark;
  position: absolute;
  z-index: 1;
  right: 100px;
  top: 60px;
  @include rotate(-30);
  text-align: center;
  line-height: 1;
  small {
    @include css-lock(21, 31, 320, 1199, font-size);
    display: block; } }
