.btn {
  display: inline-block;
  text-align: center;
  background-color: $accent;
  border: 2px solid  $accent;
  border-radius: 35px;
  padding: 17px 65px;
  font-size: $fz-sm;
  @include bold;
  color: $white;
  @include tr(color .3s, background-color .3s, border-color .3s);
  @include hover {
    background-color: transparent;
    color: $accent; }
  @include lg {
    padding: 10px 50px; }
  @include sm {
    font-size: $fz-ssm;
    padding-left: 25px;
    padding-right: 25px; }

  &--transp-purple {
    background-color: transparent;
    color: $purple;
    border-color: $purple;
    @include hover {
      background-color: $purple;
      border-color: $purple;
      color: $white; } }
  &--full {
    padding-left: 5px;
    padding-right: 5px;
    width: 100%;
    @include lg {
      padding-left: 5px;
      padding-right: 5px; } }
  &--sm {
    padding-left: 38px;
    padding-right: 38px;
    @include lg {
      padding-left: 30px;
      padding-right: 30px; } }
  &--lg {
    min-width: 295px;
    padding-left: 10px;
    padding-right: 10px;
    @include sm {
      min-width: 0; } } }

.btn-link {
  @include reg;
  color: $purple;
  @include inline-flex(flex-start, center);
  @include hover {
    text-decoration: underline; }
  .icon {
    fill: $accent;
    margin-right: 10px; } }
