.slider {
  $this: &;
  .swiper-container, .swiper-wrapper, .swiper-slide {
    box-sizing: border-box; }
  .swiper-slide {
    height: auto; }
  &__wrap {
    position: relative; }
  &__prev,
  &__next {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    z-index: 1;
    display: inline-block;
    height: 45px;
    width: 45px;
    background-color: $yellow;
    color: $white;
    border-radius: 50%;
    @include tr(color .3s);
    @include before {
      @include icon-arrow(14, 14, 2, currentColor, 45);
      display: inline-block;
      @include vcenter;
      transform: translate(-50%, -50%) rotate(45deg); } }
  &__prev {
    left: 0;
    @include before {
      left: 25px; } }
  &__next {
    right: 0;
    transform: translate(0, -50%) rotate(180deg);
    @include before {
      left: 25px; } }
  &__prev-simple,
  &__next-simple {
    background-color: transparent;
    color: $c-text; }
  &__prev-purple,
  &__next-purple {
    background-color: $purple; }
  &__prev-sm,
  &__next-sm {
    width: 25px;
    height: 25px;
    @include before {
      width: 8px;
      height: 8px;
      @include vcenter;
      transform: translate(-35%, -50%) rotate(45deg); } }


  &--center {
    &__wrap {
      .swiper-container {
        padding-top: 30px;
        padding-bottom: 30px;
        max-width: 1230px;
        margin-left: auto;
        margin-right: auto;
        // padding-left: 90px
        // padding-right: 90px
        @include sm {
          padding-top: 0;
          padding-bottom: 0; } } }
    .slide {
      @include tr(transform .3s);
      &.swiper-slide-active {
        @include scale(1.2);
        z-index: 1;
        @include sm {
          @include scale(1); }
        .slide__img {
          @include before {
            visibility: hidden;
            opacity: 0; } } }
      &__img {
        border-radius: $radius;
        @include before {
          @include coverdiv;
          background-color: rgba($purple, .5);
          border-radius: $radius;
          @include tr(opacity .3s, visibility .3s); } } } }
  &--testimonials {
    &__wrap {
      #{$this}__prev {
        // left: -70px
        left: -10%;
        @include xs {
          left: 0; } }
      #{$this}__next {
        // right: -70px
        right: -10%;
        @include xs {
          right: 0; } } } }
  &--gallery {
    .slide {
      &__img {
        padding-top: 100%;
        border-radius: $radius; } }
    &__wrap {
      max-width: 500px;
      margin-left: auto;
      margin-right: auto;
      .swiper-container {
        max-width: 400px;
        margin-left: auto;
        margin-right: auto; } } } }




.slide {
  display: block;
  &__img {
    padding-top: 60%;
    display: block;
    @include cover;
    position: relative;
    @include md {
      padding-top: 80%; }
    @include sm {
      padding-top: 50%; } } }
