.price-card {
  background-color: $white;
  box-shadow: 0px 2px 17px 0px rgba(0, 0, 0, 0.35);
  border-radius: $radius;
  @include flex(center, center, column);
  padding-top: 65px;
  padding-bottom: 60px;
  width: 360px;
  color: $green-light;
  @include sm {
    width: auto;
    padding-top: 20px;
    padding-bottom: 20px; }
  &__title {
    text-align: center;
    small {
      display: block;
      color: $gray;
      font-size: 21px; } }
  &__price {}


  &--accent {
    color: $accent;
    border: 2px solid  $accent;
    width: 370px;
    margin-left: -5px;
    padding-top: 65px;
    padding-bottom: 60px;
    @include sm {
      margin-right: -5px; } } }
