.form {
  box-shadow: 0px 2px 19px 0px rgba(0, 0, 0, 0.19);
  border-radius: $radius;
  background-color: $white;
  padding: 50px 35px 30px;
  @include lg {
    padding: 30px 20px 20px; }
  @include md {
    padding: 20px; }
  @include sm {
    padding: 15px; }
  &__field {
    margin-bottom: 25px;
    @include lg {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 15px; } }
  &__title {
    color: $c-text;
    text-align: center;
    margin-bottom: 30px;
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 10px; } } }
